html {
	height: 100%;
	width: 100%;
}

body {
	height: 100%;
	width: 100%;
	overflow: auto;
}

.container {
	width: calc(100% - 50px);
	margin: 0 auto;
	display: flex;
	justify-content: space-between;
}

.col-left {
	width: 350px;
}

.col-mid {
	width: 900px;
}

.col-right {
	width: 350px;
}

.main-content {
	padding: 40px 0;
}
